<template>
  <section>
    <div class="container">
      <div class="debt-regist">
        <p class="title"><em>  </em><br></p>
        <div class="list">
          <div>
            <ul class="flex">
              <li class="key"></li>
              <li class="value">
                <multiselect :id="'cd_assets_class'" v-model="cd_assets_class" ref="cd_assets_class" key="cd_assets_class" placeholder="" :title="''" :options="assetsClassOptions" :onClose="selectAssetsClass" v-validate="'multi'" data-vv-name=''>
                </multiselect>
              </li>
            </ul>
            <p class="warn" v-if="errors.has('')" v-html="'   .'"></p>
          </div>

          <!---->
          <template v-if="showKey=='30'">
            <div>
              <ul class="flex">
                <li class="key"></li>
                <li class="value">
                  <multiselect :id="'cd_class_prop'" v-model="cd_class_prop" ref="cd_class_prop" key="cd_class_prop" :title="''" :options="option_class_prop" v-validate="'multi'" data-vv-name=''>
                  </multiselect>
                </li>
              </ul>
              <p class="warn" v-if="errors.has('')" v-html="'   .'"></p>
            </div>
            <div>
              <ul class="flex">
                <li class="key"></li>
                <li class="value">
                  <input type="text" v-model="view_addr" readonly="readonly">
                  <input type="hidden" v-model="real_estate_addr" key="real_estate_addr" v-validate="'required'" data-vv-name=''>
                  <button class="search" @click="scAddress()"></button>
                </li>
              </ul>
              <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
            </div>
          </template>

          <!---->
          <div v-if="showKey=='40'">
            <ul class="flex">
              <li class="key"></li>
              <li class="value">
                <input type="text" v-model="nm_model" key="nm_model" v-validate="'required|max:15'" data-vv-name=''>
              </li>
            </ul>
            <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
          </div>

          <!---->
          <template v-if="showKey=='50'">
            <div>
              <ul class="flex">
                <li class="key"></li>
                <li class="value">
                  <multiselect :id="'cd_class_nbmt'" v-model="cd_class_nbmt" ref="cd_class_nbmt" key="cd_class_nbmt" :title="''" :options="option_class_nbmt" v-validate="'multi'" data-vv-name=''>
                  </multiselect>
                </li>
              </ul>
              <p class="warn" v-if="errors.has('')" v-html="'   .'"></p>
            </div>
            <div>
              <ul class="flex">
                <li class="key"></li>
                <li class="value">
                  <input type="number" v-model="amount_jewelry" key="amount_jewelry" v-validate="'required|numeric|max:12'" data-vv-name=''>g
                </li>
              </ul>
              <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
            </div>
          </template>

          <!---->
          <template v-if="showKey=='60'">
            <div>
              <ul class="flex">
                <li class="key"></li>
                <li class="value">
                  <multiselect :id="'cd_class_frcr'" v-model="cd_class_frcr" ref="cd_class_frcr" key="cd_class_frcr" :title="''" :options="option_class_frcr" v-validate="'multi'" data-vv-name=''>
                  </multiselect>
                </li>
              </ul>
              <p class="warn" v-if="errors.has('')" v-html="'   .'"></p>
            </div>
            <div>
              <ul class="flex">
                <li class="key"></li>
                <li class="value">
                  <money v-model="amt_balance" key="amt_balance" v-validate="'required|numeric|max:12'" data-vv-name='' />
                </li>
              </ul>
              <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
            </div>
          </template>

          <!---->
          <div v-if="showKey=='90'">
            <ul class="flex">
              <li class="key"></li>
              <li class="value">
                <input type="text" v-model="etc_assets" key="etc_assets" v-validate="'required|max:15'" data-vv-name=''>
              </li>
            </ul>
            <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
          </div>

          <!---->
          <template v-if="showKey!=''">
            <!--/-->
            <div v-if="showKey=='60'">
              <ul class="flex">
                <li class="key"></li>
                <li class="value">
                  <money v-model="amt_evaluation_frcr" key="amt_evaluation_frcr" v-validate="'required|numeric|max:12'" data-vv-name='' />
                </li>
              </ul>
              <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
            </div>
            <div v-else>
              <ul class="flex">
                <li class="key"></li>
                <li class="value">
                  <money v-model="amt_evaluation" key="amt_evaluation" v-validate="'required|numeric|max:12'" data-vv-name='' />
                </li>
              </ul>
              <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
            </div>
            <ul class="block">
              <li class="key"></li>
              <li class="value">
                <input type="text" v-model="memo" key="memo" v-validate="'max:15'" data-vv-name=''>
                <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
              </li>
            </ul>
          </template>
        </div>

      </div>
      <div v-if="isShowBtn" @click="createAssets()" class="btn-wrap float">
        <a class="solid blue box"></a>
      </div>
    </div>

    <vue-modal transitionName="zoom-in" name="postcode-modal">
      <div slot="body">
        <div class="v-modal__heading">
          <div class="v-modal__title"></div>
          <span @click="closePostcodeMd()" class="v-modal__close-btn">&times;</span>
        </div>
        <DaumPostcode :on-complete="selectPostcode" />
      </div>
    </vue-modal>
  </section>
</template>

<script>
import Common from "./../../assets/js/common.js";
import Constant from "./../../assets/js/constant.js";
import ko from "vee-validate/dist/locale/ko.js";

import DaumPostcode from "vuejs-daum-postcode";

export default {
  name: "assetsDirInput",
  data() {
    return {
      showKey: "",
      isShowModal: false,
      isShowBtn: false,
      assetsClassOptions: [
        { text: "", value: "30" },
        { text: "", value: "40" },
        { text: "", value: "50" },
        { text: "", value: "60" },
        { text: "", value: "90" }
      ],
      cd_assets_class: "",
      cd_detail_class: "",
      option_class_prop: "", // - (option)
      cd_class_prop: "", // - 
      view_addr: "", // - (view)
      real_estate_addr: "", // - 
      nm_model: "", // - 
      option_class_nbmt: "", // - (option)
      cd_class_nbmt: "", // - 
      amount_jewelry: "", // - 
      option_class_frcr: "", // - (option)
      cd_class_frcr: "", // - 
      amt_balance: "", // - 
      amt_evaluation_frcr: "", // - 
      etc_assets: "", // - 
      amt_evaluation: "", // - 
      memo: "" // - 
    };
  },
  components: {
    DaumPostcode
  },
  computed: {},
  watch: {
    isShowModal: function(param) {
      if (param) {
        this.$modals.show("postcode-modal");
      } else {
        this.$modals.hide("postcode-modal");
      }
    }
  },
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = " ";
  },
  created() {
    this.option_class_prop = Common.makeOptions("cd_assets_prop", "");
    this.option_class_prop.unshift({ text: "", value: "" });
    this.option_class_nbmt = Common.makeOptions("cd_assets_nbmt", "");
    this.option_class_nbmt.unshift({ text: "", value: "" });
    this.option_class_frcr = Common.makeOptions("cd_assets_frcr", "");
    this.option_class_frcr.unshift({ text: "", value: "" });
    this.cd_class_prop = this.option_class_prop[0];
    this.cd_class_nbmt = this.option_class_nbmt[0];
    this.cd_class_frcr = this.option_class_frcr[0];
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    //
    selectAssetsClass: function(param) {
      var _this = this;

      if (_this.showKey == param.value) return false;

      _this.errors.clear();
      _this.initForm();

      _this.showKey = param.value;

      _this.isShowBtn = true;
    },
    initForm: function() {
      var _this = this;
      _this.cd_class_prop = _this.option_class_prop[0];
      _this.cd_class_nbmt = _this.option_class_nbmt[0];
      _this.cd_class_frcr = _this.option_class_frcr[0];
      _this.view_addr = "";
      _this.real_estate_addr = "";
      _this.nm_model = "";
      _this.amount_jewelry = "";
      _this.amt_balance = "";
      _this.amt_evaluation_frcr = "";
      _this.etc_assets = "";
      _this.amt_evaluation = "";
      _this.memo = "";
    },
    //
    scAddress: function() {
      this.isShowModal = true;
    },
    //close
    closePostcodeMd: function() {
      this.isShowModal = false;
    },
    //   
    selectPostcode: function(param) {
      var _this = this;
      console.log(param.address);
      _this.view_addr = param.address;
      _this.real_estate_addr = param.address;
      this.isShowModal = false;
    },
    formatNumber: function(data) {
      return Common.formatNumber(data);
    },
    createAssets: function() {
      var _this = this;

      this.$validator.validateAll().then(res => {
        if (res) {
          Constant.options.title = "FINSET";
          this.$dialogs
            .confirm(" ?", Constant.options)
            .then(res => {
              if (res.ok) {
                var cd_assets_class = _this.cd_assets_class.value;
                var formData = new FormData();
                formData.append("cd_assets_class", cd_assets_class);
                if ("30" == cd_assets_class) {
                  formData.append("cd_detail_class", _this.cd_class_prop.value);
                } else if ("50" == cd_assets_class) {
                  formData.append("cd_detail_class", _this.cd_class_nbmt.value);
                } else if ("60" == cd_assets_class) {
                  formData.append("cd_detail_class", _this.cd_class_frcr.value);
                }
                formData.append("real_estate_addr", _this.real_estate_addr);
                formData.append("nm_model", _this.nm_model);
                formData.append("amount_jewelry", _this.amount_jewelry);
                formData.append("amt_balance", _this.amt_balance);
                formData.append("etc_assets", _this.etc_assets);
                if ("60" == cd_assets_class) {
                  formData.append("amt_evaluation", _this.amt_evaluation_frcr);
                } else {
                  formData.append("amt_evaluation", _this.amt_evaluation);
                }
                formData.append("memo", _this.memo);

                this.$http
                  .post("/m/assets/createAssetsInfo.json", formData)
                  .then(response => {
                    this.$toast.center(response.data.message);
                    if ("00" == response.data.result) {
                      _this.$router.push("/assets/main");
                    }
                  })
                  .catch(e => {
                    this.$toast.center(ko.messages.error);
                  });
              }
            });
        }
      });
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



