<template>
  <div>
    <button class="open-menu"></button>
    <div class="gnb-wrap">
      <div class="top">
        <div class="left">
          <button class="alarm" @click="clickButton('/etc/alarmHistory', '/main')"></button>
          <button class="logout" @click="chkLogout"></button>
        </div>
        <div class="right">
          <button class="gnb-close"></button>
        </div>
      </div>
      <div class="scroll-wrap">
        <div class="info">
          <p class="name">{{this.$store.state.user.nmPerson}}</p>
          <p class="date">  {{connectTime}} </p>
          <a @click="clickButton('/mypage/info', '/main')">MYPAGE</a>
        </div>
        <div class="links">
          <a @click="clickButton('/etc/faqMain', '/main')"></a>
          <a @click="clickButton('/mypage/cert', '/main')"></a>
          <a @click="clickButton('/etc/noticeMain', '/main')">/</a>
          <a @click="clickButton('/news/main', '/main')"></a>
          <a @click="clickButton('/scrap/ctrlFcLink', '/main')"></a>
          <a @click="clickButton('/share/main?cd_share=02', '/credit/main')"></a>
          <a @click="clickButton('/common/monthCal', '/main')"></a>
          <a @click="clickButton('/debt/calc', '/main')"></a>
        </div>
        <dl id="gnb">
          <dt></dt>
          <dd><a @click="clickButton('/credit/main', '')"></a></dd>
          <dd><a @click="clickButton('/credit/raiseMain', '/credit/main')"></a></dd>
          <dd><a @click="clickButton('/credit/smartReport', '/credit/main')"></a></dd>
          <dd><a @click="clickButton('/credit/counselMain', '/credit/main')"></a></dd>
          <dd><a @click="clickButton('/share/main?cd_share=01', '/credit/main')"></a></dd>

          <dt></dt>
          <dd><a @click="clickButton('/debt/main', '')"></a></dd>
          <dd><a @click="clickButton('/debt/calc', '/debt/main')"></a></dd>
          <dd><a @click="clickButton('/debt/reqIntrCut', '/debt/main')"></a></dd>
          <dd><a @click="clickButton('/goods/list', '/debt/main')"></a></dd>

          <dt></dt>
          <dd><a @click="clickButton('/consume/main', '')"></a></dd>
          <dd><a @click="clickButton('/common/monthCal', '/consume/main')"></a></dd>
          <dd><a @click="clickButton('/consume/payment', '/consume/main')"></a></dd>

          <dt></dt>
          <dd><a @click="clickButton('/assets/main', '')"></a></dd>
          <dd><a @click="clickButton('/assets/bankMain', '/assets/main')"></a></dd>
          <dd><a @click="clickButton('/assets/stockMain', '/assets/main')"></a></dd>
          <dd><a @click="clickButton('/assets/etcMain', '/assets/main')"></a></dd>
        </dl>
      </div>
    </div>
  </div>
</template>

<script>
import Constant from "./../../assets/js/constant.js";

export default {
  name: "error",
  data() {
    return {
      errMsg: "",
      connectTime: ""
    };
  },
  component: {},
  // computed () {
  // },
  beforeCreate() {},
  created() {
    this.getConnectTime();
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    //logout 
    chkLogout: function() {
      let _this = this;
      _this.$dialogs
        .confirm(" ?", Constant.options)
        .then(res => {
          if (res.ok) {
            _this.$router.push("/mypage/logout");
          } else {
            return false;
          }
        });
    },
    //  
    getConnectTime: function() {
      var _this = this;
      this.$http
        .get("/m/person/getPersonConnectTime.json", {
          params: {}
        })
        .then(response => {
          _this.connectTime = response.data.connectTime;
        })
        .catch(e => {
          this.$toast.center(ko.messages.error);
        });
    },
    clickButton: function(page, back) {
      // GNB Close Event
      if (
        page === "/debt/calc" ||
        page === "/credit/counselMain" ||
        page === "/assets/stockMain"
      ) {
        this.$dialogs.alert("  .", Constant.options);
        return false;
      }
      $(".gnb-wrap").removeClass("on");
      $("body").removeClass("not-scroll");
      // if (this.$store.state.header.type === "sub" && back) {
      //   this.$store.state.header.fromPath = back;
      // }

      this.$router.push(page);
    }

    // goBack: function() {
    //   var historySize = history.length;
    //   if (historySize == undefined || historySize == 0) {
    //     if (Constant.userAgent == "Android") {
    //       window.Android.exitApp();
    //     }
    //   } else {
    //     history.back();
    //   }
    // }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



