<template>
  <section v-if="seen">
    <div class="mypage-top">
      <p class="key"></p>
      <p class="value">{{nm_person}}</p>
      <p class="key"></p>
      <p class="value">{{viewHp}}</p>
      <p class="key"></p>
      <p class="value email" id="email">{{email}}</p>
      <p class="link"><a @click="$router.push('/mypage/email')"></a></p>
    </div>

    <div class="mypage-links">
      <!-- <a @click="$router.push('')"></a> -->
      <a @click="$router.push('/mypage/regAlarm')"></a>
      <a @click="$router.push('/mypage/cert')">/</a>
      <a @click="$router.push('/share/main')"></a>
      <a @click="$router.push('/scrap/ctrlFcLink')"></a>
      <!-- <a @click="$router.push('/credit/counselMain')"></a> -->
      <a @click="clickButton('/credit/counselMain', '/credit/main')"></a>
      <!-- <a @click="$router.push('/mypage/favGoods')"></a> -->
      <a @click="$router.push('/mypage/rstlInqGoods')"></a>
      <!-- <a @click="$router.push('/mypage/state')"></a> -->
    </div>

    <div class="member-exit">
        <a @click="$router.push('/mypage/drop')"></a> 
    </div>

  </section>
</template>

<script>
import Constant from "./../../assets/js/constant.js";
export default {
  name: "MypageInfo",
  data() {
    return {
      no_person: this.$store.state.user.noPerson,
      nm_person: this.$store.state.user.nmPerson,
      hp: this.$store.state.user.hp,
      viewHp: "",
      email: "",
      seen: false
    };
  },
  components: {},
  computed: {},
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = "MYPAGE";
    // this.$store.state.header.backPath = "/main";
  },
  created() {
    var _this = this;
    var frm = new FormData();
    var personInfo;
    frm.append("no_person", _this.no_person);
    frm.append("hp", _this.hp);
    _this.$http
      .post("/m/customercenter/getCustomerCenterMain.json", frm)
      .then(response => {
        personInfo = response.data.personVO;
        var tEmail = personInfo.email;
        if (tEmail != null && tEmail != "") {
          _this.email = tEmail;
          localStorage.removeItem("email");
          localStorage.setItem("email", tEmail);
        } else {
          _this.email = "";
        }
        _this.viewHp = personInfo.hp;
        _this.seen = true;
      });
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    clickButton: function(page, back) {
      // GNB Close Event
      if (page === "/credit/counselMain") {
        this.$dialogs.alert("  .", Constant.options);
        return false;
      }
      // if (this.$store.state.header.type === "sub" && back) {
      //   this.$store.state.header.fromPath = back;
      // }

      this.$router.push(page);
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



